@media (min-width: 320px) and (max-width: 480px) {
  /* Your CSS rules here */

  .navbar-container {
    background-color: #282133;
    width: 85.34vw;
    height: 5vh;
    backdrop-filter: blur(4px);

    border-radius: 50px;
    display: flex;
    /* justify-content: space-between; */
    align-items: center;
  }

  .navbar-image {
    width: 15%;
    padding-left: 5px;
  }
  .navbar-bitmoji {
    padding-left: 10px;
    height: 5vh;
    width: 10vw;
  }
  .navbar-name {
    color: whitesmoke;
    width: 55%;
    height: 23px;
    padding-left: 12px;
    padding-top: 2px;
    font-family: "Roboto Condensed";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    animation-duration: 5s;
    animation-name: fadeInLeft;
  }

  .navbar-name span {
    text-align: center;
    opacity: 0.8;
  }

  @keyframes fadeInLeft {
    0% {
      opacity: 0;
      transform: translateX(-50px);
    }
    50% {
      opacity: 0.1;
      transform: translateX(0);
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }

  .navbar-menu-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    padding: 5px 0px;
    gap: 5px;
    width: 20%;
    height: 25px;
    /* padding-right: 20px; */
    opacity: 0.8;
  }

  .line-1,
  .line-2,
  .line-3 {
    width: 25px;
    height: 2px;
    background: #d9d9d9;
    border-radius: 50px;
  }
}
